h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
p {
  margin: 0;
  padding: 0;
}
a {
  display: block;
  margin: 0;
  padding: 0;
}
button {
  display: block;
  cursor: pointer;
  border: transparent;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
label,
input,
textarea {
  display: block;
}
img,
svg {
  display: block;
}
